.info{
  padding: 5.625em 0 19em;
  &__inner{
    position: relative;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .75em));
    gap: 1.5em;
  }
  &__img{
    position: absolute;
    left: .75em;
    bottom: -19em;
    transform: rotate(11deg);
    width: 27em;
  }
  &__img2{
    display: none;
  }
}

.info-card{
  padding: 2.8125em;
  border-radius: 2.5em;
  background-color: $mauve2;
  &--yellow{
    background-color: $golden-fizz;
  }
  &--gray{
    background-color: $alto;
  }
  &--big{
    grid-column: 1/-1;
    min-height: 34.75em;
    .h2{
      text-align: center;
    }
  }
  .text{
    margin-top: 1.25em;
    a{
      text-decoration: underline;
      word-break: break-word;
      &:hover{
        color: inherit;
        text-decoration-color: transparent;
      }
    }
  }
  &__items{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .75em));
    gap: 1.5em;
  }
  &__item{
    margin-bottom: 1.25em;
    font-size: 1.25em;
    line-height: 130%;
    padding-left: 2.5em;
    position: relative;
    &:last-child{
      margin-bottom: 0;
    }
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 1.6em;
      height: 1.6em;
      background-image: url(../img/check.svg);
      @include bgImgSize(100%);
    }
  }
}

.text{
  font-size: 1.25em;
  p,ol,ul{
    margin-bottom: 1em;
    &:last-child{
      margin-bottom: 0;
    }
  }

  p,li{
    line-height: 130%;
  }
  ol {
    @include grid100gap(.375em);
    counter-reset: list;
    li {
      counter-increment: list;
      position: relative;
      padding-left: 1.25em;
      &:before {
        content: counter(list) '. ';
        position: absolute;
        left: 0;
        top: 0;
        padding-left: .1875em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .info{
    padding: 0 0 19.25em;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .5625em));
      gap: 1.125em;
    }
    &__img{
      bottom: -19.25em;
      width: 17.5em;
      left: 0;
    }
    &__img2{
      display: block;
      position: absolute;
      right: -2.8125em;
      bottom: -19em;
      width: 10.5em;
    }
  }

  .info-card{
    padding: 2.375em;
    &--big{
      .h2{
        text-align: left;
      }
    }
    .h2{
      font-size: 1.875em;
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - .5625em));
      gap: 1.125em;
      margin-top: 2em;
    }
    &__item{
      font-size: 1em;
      margin-bottom: 1.25em;
    }
  }

  .text{
    font-size: 1em;
  }
}


@media screen and (max-width: 576px){
  .info{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .info-card{
    &__items{
      grid-template-columns: 100%;
    }
  }
}
