@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2.375em;
    height: 4.5em;
    border-radius: 2.1875em;
    font-weight: 700;
    text-transform: uppercase;
    font-size: .7em;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.5625em;
    }
}

.m-btn-green{
    background-color: $mauve;
    color: $black;
    &:hover{
        background-color: darken($mauve,10%);
        color: $black;
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        padding: 0 1.875em;
        height: 3.625em;
        border-radius: 1.875em;
        span{
            font-size: 1.25em;
        }
    }
}
