.facts{
  padding: 4em 0 2em;
  &__inner{
    position: relative;
  }
  &__img{
    position: absolute;
    top: -4em;
    left: -10.125em;
    width: 31.5em;
    transform: rotate(155deg);
    z-index: 2;
  }
  &__img2{
    position: absolute;
    top: 3.25em;
    right: -5em;
    width: 18.875em;
    transform: rotate(9deg);
    z-index: 2;
  }
  .h2{
     text-align: center;
     br{
       display: none;
     }
  }
  &__grid{
    margin-top: 1.25em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2.75em / 3));
    gap: 1.375em;
  }
  &__bottom{
    width: 51em;
    max-width: 100%;
    text-align: center;
    margin: 1.25em auto 0;
  }
}

.fact-card{
  padding: 2.8125em;
  border-radius: 2.5em;
  background-color: $alto;
  position: relative;
  overflow: hidden;
  &__img{
    position: absolute;
    width: 100%;
    bottom: 0;
    left: 0;
    &._view2{
      display: none;
    }
  }
  &__text{
    line-height: 133%;
    position: relative;
    z-index: 2;
  }
  @media screen and (min-width: 993px){
    &:nth-child(odd){
      background-color: $mauve2;
    }
    &:nth-child(1){
      grid-column: 1/2;
      grid-row: 1/3;
      display: flex;
      align-items: flex-end;
      padding-bottom: 4em;
      min-height: 25em;
    }
    &:nth-child(2){
      grid-column: 1/2;
      grid-row: 3/4;
    }
    &:nth-child(3){
      grid-column: 2/3;
      grid-row: 1/2;
    }
    &:nth-child(4){
      grid-column: 2/3;
      grid-row: 2/3;
      display: flex;
      align-items: center;
    }
    &:nth-child(5){
      grid-column: 2/3;
      grid-row: 3/4;
    }
    &:nth-child(6){
      grid-column: 3/4;
      grid-row: 1/3;
      background-color: $mauve2;
      .fact-card__text{
        max-width: 10.625em;
      }
    }
    &:nth-child(7){
      grid-column: 3/4;
      grid-row: 3/4;
      background-color: $alto;
      .fact-card__text{
        max-width: 13em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .facts{
    padding: 2.375em 0 1.25em;
    &__img{
      width: 18.75em;
      left: unset;
      right: -5em;
    }
    &__img2{
      right: -2.8125em;
      top: unset;
      bottom: -2em;
      width: 16.875em;
      transform: rotate(0);
    }
    .h2{
      text-align: left;
      br{
        display: unset;
      }
    }
    &__grid{
      grid-template-columns: 100%;
      gap: .875em;
    }
    &__bottom{
      margin: 3em 0 0;
      text-align: left;
      max-width: 13.125em;
    }
  }

  .fact-card{
    padding: 2.375em 2em;
    &__text{
      font-size: 1.25em;
    }
    &__img{
      &._view1{
        display: none;
      }
      &._view2{
        display: block;
      }
    }
    &:nth-child(1),
    &:nth-child(3),
    &:nth-child(5),
    &:nth-child(6){
      background-color: $mauve2;
    }
    &:nth-child(1){
      order: 1;
      br{
        display: none;
      }
      .fact-card__text{
        max-width: 16.25em;
      }
    }
    &:nth-child(2){
      order: 2;
      .fact-card__text{
        max-width: 14em;
      }
    }
    &:nth-child(3){
      order: 3;
      .fact-card__text{
        max-width: 14em;
      }
    }
    &:nth-child(4){
      order: 4;
    }
    &:nth-child(5){
      order: 5;
      br{
        display: none;
      }
    }
    &:nth-child(7){
      order: 6;
      .fact-card__text{
        max-width: 13em;
      }
    }
    &:nth-child(6){
      order: 7;
    }
  }
}


