.main{
  padding: 1.875em 0;
  background-color: $mercury;
  &__inner{
    display: grid;
    grid-template-columns: 25.5em calc(100% - 25.5em);
    position: relative;
  }
  &__img{
    position: absolute;
    top: -8em;
    left: -10.25em;
    width: 36.5625em;
  }
  &__title{
    max-width: 25.5em;
    margin-top: 1em;
  }
  &__salary{
    margin-top: 1.125em;
  }
  &__nav{
    margin-top: 2.5em;
  }
  &__desc{
    margin-top: 2.5em;
    max-width: 28.5em;
  }
}

.research{
  padding: 7em 0 4em;
  &__top,
  &__bottom{
    display: grid;
    grid-template-columns: calc(100% - 36.125em) 32.625em;
    gap: 3.5em;
    position: relative;
  }
  &__top{
    &-col{
      display: flex;
      align-items: flex-end;
      padding-left: 10.25em;
    }
  }
  &__img{
    img{
      object-fit: cover;
      height: 100%;
      width: 100%;
      display: block;
      border-radius: 2.5em;
    }
  }
  &__logo{
    width: 13.75em;
  }
  .socials{
    position: absolute;
    left: 0;
    bottom: 100%;
    margin-bottom: 3em;
  }
  &__bottom{
    margin-top: 3em;
  }
  &__images{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.0625em));
    gap: 2.125em;
  }
  &__peach{
    position: absolute;
    top: -8.5em;
    left: -8.25em;
    width: 28em;
  }
}

.about{
  &__desc{
    margin-top: 1.875em;
    .text20{
      p{
        line-height: inherit;
        margin-bottom: 1em;
        &:last-child{
          margin-bottom: 0;
        }
      }
    }
  }
}


@media screen and (max-width: 992px){
  .wrapper{
    overflow: hidden;
  }

  .main{
    padding: 3.5em 0 4.375em;
    &__inner{
      grid-template-columns: 100%;
    }
    &__img{
      left: unset;
      right: -2em;
      top: -9.25em;
      width: 12.5em;
      transform: rotate(-52deg);
    }
    &__nav{
      margin-top: 1.25em;
    }
    &__desc{
      margin-top: 1.75em;
      max-width: 12.75em;
    }
  }

  .research{
    padding: 4.75em 0 2.375em;
    &__top,
    &__bottom{
      grid-template-columns: repeat(2,calc(50% - .375em));
      gap: .75em;
    }
    &__top{
      &-col{
        justify-content: flex-end;
        padding-left: 0;
      }
    }
    &__logo{
      width: 9.6875em;
    }
    &__img{
      min-height: 100%;
    }
    &__images{
      grid-template-columns: repeat(2,calc(50% - .5625em));
      gap: 1.125em;
    }
    &__img{
      border-radius: 2.5em;
      @include imgCover(105%);
    }
    &__peach{
      width: 17em;
      top: -15em;
      right: -4.25em;
      left: unset;
      transform: rotate(-15deg);
    }
  }
}


@media screen and (max-width: 576px){
  .research{
    &__top{
      grid-template-columns: calc(100% - 14.875em - 1.25em) 14.875em;
      gap: 1.25em;
    }
    &__bottom{
      grid-template-columns: 100%;
      gap: 2.25em;
      &-col2{
        order: -1;
      }
    }
    &__images{
      grid-template-columns: 10em calc(100% - 11.125em);
      .research__img{
        height: 10.5625em;
        padding: 0;
      }
    }
    .socials{
      position: static;
      flex-direction: row;
      margin: 0 0 0 3.5em;
      &__item{
        @include lastM(0 1.4375em 0 0);
      }
    }
  }

  .about{
    &__top{
      display: flex;
      align-items: center;
    }
  }
}
