.header{
  padding: 1.25em 0;
  &__inner{
    display: grid;
    grid-template-columns: 25.5em calc(100% - 25.5em);
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 9.5em;
    margin-top: .5em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.nav{
  max-width: 26em;
  display: flex;
  flex-wrap: wrap;
  &__link{
    font-size: .875em;
    line-height: 121%;
    margin: .71em 2.6em 0 0;
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: 1.25em 0;
    &__inne{
      display: block;
    }
    &__logo{
      margin: 0;
    }
  }

  .nav{
    display: none;
  }
}


