.for-who{
  padding: 2em 0 3.375em;
  &__list{
    margin-top: 2.375em;
    column-count: 2;
    column-gap: 2.125em;
    li{
      font-size: 1.25em;
      line-height: 120%;
      padding-left: 1.2em;
      position: relative;
      margin-bottom: 1.2em;
      display: inline-block;
      width: 100%;
      &:before{
        content: '';
        position: absolute;
        top: .3em;
        left: 0;
        width: .6em;
        height: .6em;
        background-color: #D9D9D9;
        border-radius: 50%;
      }
    }
  }
}

.like-mind{
  padding: 2em 0;
  .h2{
    text-align: center;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2.75em / 3));
    gap: 1.375em;
    margin-top: 1.5em;
  }
  &__item{
    position: relative;
    &-img{
      @include imgCover(100%);
      border-radius: 2.5em;
      ._view2{
        display: none;
      }
    }
    &-text{
      position: absolute;
      width: 100%;
      padding: 1.875em;
      text-align: center;
      top: 0;
      left: 0;
    }
    &--bottom{
      .like-mind__item-text{
        top: unset;
        bottom: 0;
      }
    }
  }
  &__text{
    margin-top: 1.875em;
    text-align: center;
  }
}


@media screen and (max-width: 992px){
  .for-who{
    padding: 1.25em 0;
    &__list{
      margin-top: 2em;
    }
  }

  .like-mind{
    padding: 1.25em 0;
    &__items{
      grid-template-columns: repeat(2,calc(50% - .4375em));
      gap: .875em;
      margin-top: 1em;
    }
    &__text{
      margin-top: 1.75em;
    }
  }
}


@media screen and (max-width: 576px){
  .for-who{
    &__list{
      column-count: 1;
    }
  }

  .like-mind{
    .like-mind{
      &__items{
        grid-template-columns: 100%;
      }
      &__item{
        &-img{
          padding-bottom: 61%;
          ._view1{
            display: none;
          }
          ._view2{
            display: block;
          }
        }
        &-text{
          padding: 1.5em;
        }
      }
    }
  }
}
