.finish{
  background-color: $mercury;
  padding: 4.375em 0 2.5em;
  &__inner{
    position: relative;
  }
  &__top{
    text-align: center;
    @include grid100gap(1em);
  }
  &__desc{
    width: 31em;
    max-width: 100%;
    margin: 0 auto;
  }
  &__nav{
    margin-top: 2.5em;
  }
  &__img{
    position: absolute;
    bottom: .5em;
    left: -5em;
    width: 17em;
    transform: rotate(-29deg);
  }
  &__img2{
    position: absolute;
    bottom: -1.5em;
    left: -6.875em;
    width: 20.5em;
  }
  &__img3{
    position: absolute;
    width: 32.625em;
    right: -17em;
    bottom: -10.25em;
    transform: rotate(55deg);
  }
  &__img4{
    display: none;
  }
}

.timer{
  margin: 3.125em;
  display: flex;
  justify-content: center;
}

.countdown{
  display: flex;
  &__item{
    margin-left: -.1875em;
    &:first-child{
      margin-left: 0;
    }
  }
}

.countdownProp{
  width: 7.75em;
  border-radius: 2.5em;
  background-color: $white;
  margin-right: 1.5em;
  &:last-child{
    margin-right: 0;
  }
  &__item{
    &-top{
      height: 7.125em;
      @include center;
    }
    &-bottom{
      background-color: $golden-fizz;
      height: 3.125em;
      border-radius: 0 0 2.5em 2.5em;
      display: flex;
      justify-content: center;
      padding: .5em;
    }
  }
  &__num{
    *{
      display: inline;
    }
  }
  &__title{
    font-size: 1.375em;
  }
}

.atmosphere{
  padding: 3.375em 0 5.375em;
  &__grid{
    display: grid;
    grid-template-columns: 31em calc(100% - 33.75em);
    gap: 2.75em;
    align-items: center;
  }
  &__col2{
    max-width: 24.875em;
  }
  &__img{
    width: 100%;
    display: block;
    border-radius: 2.5em;
  }
  &__arrow{
    width: 23.1875em;
    position: absolute;
    top: -12.5em;
    left: -2em;
    transform: rotate(-10deg);
  }
  &__arrow2{
    display: none;
  }
  &__title{
    position: relative;
    span{
      padding: 0 .5em;
      position: relative;
      &:before{
        content: '';
        z-index: -1;
        @include centerAbsolute;
        top: 55%;
        background-color: $mauve2;
        border-radius: .75em;
        width: 100%;
        height: 100%;
      }
    }
  }
  &__desc{
    margin-top: 1.375em;
  }
}


@media screen and (max-width: 992px){
  .finish{
    padding: 2em 0 2.625em;
    margin-bottom: 11.625em;
    &__top{
      gap: .75em;
    }
    .h1{
      font-size: 1.9375em;
    }
    .h4{
      font-size: 1em;
    }
    &__desc{
      width: 18.75em;
    }
    &__nav{
      margin-top: 2.375em;
    }
    &__img{
      bottom: -17em;
      left: -4em;
      width: 13.125em;
      transform: rotate(23deg);
    }
    &__img2{
      display: none;
    }
    &__img3{
      z-index: 2;
      transform: rotate(-17deg);
      bottom: -21.75em;
      right: -9.5em;
      width: 20.75em;
    }
    &__img4{
      position: absolute;
      width: 6em;
      top: -4.375em;
      right: -1.75em;
      display: block;
    }
  }

  .timer{
    margin-top: 1.5em;
  }

  .countdownProp{
    width: 5.625em;
    border-radius: 1.875em;
    margin-right: 1em;
    &__item{
      &-top{
        height: 5.125em;
      }
      &-bottom{
        height: 2.375em;
        border-radius: 0 0 1.875em 1.875em;
        padding: .375em;
      }
    }
    &__num{
      font-size: 2.125em;
    }
    &__title{
      font-size: .9375em;
    }
  }

  .atmosphere{
    padding: 1.25em 0 2em;
    &__grid{
      grid-template-columns: 21em calc(100% - 23.75em);
    }
    &__arrow{
      width: 15em;
      top: -8em;
    }
  }
}


@media screen and (max-width: 576px){
  .atmosphere{
    &__grid{
      grid-template-columns: 100%;
    }
    &__arrow{
      display: none;
    }
    &__arrow2{
      display: block;
      position: absolute;
      width: 14.5em;
      top: -12em;
      right: -2em;
    }
    .h1{
      font-size: 3em;
    }
  }
}
