.not-reply{
  padding-bottom: 12.5em;
  &__wrap{
    padding: 3.5em 0 3.75em;
    background: $mercury;
  }
  &__inner{
    position: relative;
  }
  &__list{
    margin-top: 2.1875em;
    max-width: 34.625em;
    @include grid100gap(1.25em);
    li{
      padding-left: 1.4em;
      position: relative;
      font-size: 1.25em;
      line-height: 120%;
      &:before{
        content: 'тип';
        position: absolute;
        left: 0;
        top: 0;
        font-weight: 500;
      }
    }
  }
  &__bottom{
    max-width: 26.25em;
    margin: 2.5em 0 0 1.75em;
  }
  &__desc{
    font-size: 1.5em;
    line-height: 125%;
    font-weight: 900;
  }
  &__nav{
    display: flex;
    position: absolute;
    top: 100%;
    left: 0;
    margin-top: 8.125em;
    width: 100%;
    justify-content: center;
  }
  &__img{
    position: absolute;
    right: -2.75em;
    bottom: -8em;
    width: 24.75em;
    transform: rotate(-32deg);
  }
}

.interview{
  padding: 2.875em 0 6.625em;
  &__top{
    @include grid100gap(1.5em);
    text-align: center;
  }
  &__bottom{
    margin: 3.75em auto 0;
    width: 48.75em;
    max-width: 100%;
  }
  &__items{
    padding: 3.125em 0 5.3125em;
    @include grid100gap(2.5em);
    position: relative;
    &:before{
      content: '';
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      background-image: url(../img/arrow.svg);
      background-position: left  bottom;
      width: 1em;
      height: 100%;
      background-size: 100% auto;
    }
  }
}

.interview-item{
  position: relative;
  &:before{
    content: '';
    position: absolute;
    top: .4375em;
    left: -1.125em;
    width: 2.25em;
    height: 2.25em;
    background-color: $black;
    border-radius: 50%;
  }
  &:last-child,
  &:first-child{
    &:before{
      width: 5em;
      height: 5em;
      background-color: $primary;
    }
  }
  &:last-child{
    &:before{
      background-color: $golden-fizz;
    }
  }
  &__inner{
    @include grid100gap(.625em);
  }
  @media screen and (min-width: 993px){
    width: 50%;
    &:nth-child(odd){
      text-align: right;
      padding-right: 3.5em;
      &:before{
        left: unset;
        right: -1.125em;
      }
    }
    &:nth-child(even){
      padding-left: 3.5em;
      margin-left: auto;
    }
    &:last-child,
    &:first-child{
      &:before{
        right: -2.5em;
      }
    }
    &:first-child{
      margin-bottom: 1.25em;
    }
  }
  &__title{
    font-size: 1.375em;
    line-height: 120%;
    font-weight: 700;
  }
}


@media screen and (max-width: 992px){
  .not-reply{
    //padding-bottom: 13.75em;
    padding-bottom: 0;
    &__wrap{
      padding: 2.75em 0;
    }
    .h1{
      font-size: 1.375em;
    }
    &__list{
      margin-top: 1.5em;
      gap: .9375em;
      li{
        font-size: .9375em;
      }
    }
    &__bottom{
      margin: 1.875em 0 0;
    }
    &__desc{
      font-size: 1.25em;
    }
    &__nav{
      margin-top: 2.5em;
      justify-content: unset;
      position: static;
    }
    &__img{
      right: -1.25em;
      bottom: -14.25em;
      width: 14.25em;
      transform: rotate(-26deg);
      display: none;
    }
  }

  .interview{
    padding: 1.125em 0 2.125em;
    &__top{
      gap: .75em;
      text-align: left;
    }
    .h2{
      font-size: 1.9375em;
    }
    &__bottom{
      margin-top: 1.25em;
    }
    &__items{
      padding: 2em 0;
      gap: 1.5em;
      &:before{
        left: -.5em;
        transform: unset;
      }
    }
  }

  .interview-item{
    padding-left: 3.625em;
    &:before{
      top: 0;
    }
    &:first-child,
    &:last-child{
      &:before{
        width: 3.125em;
        height: 3.125em;
        left: -1.5625em;
      }
    }
  }
}


